body {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  width: 100%;
  min-height: 100%;
  margin: 0 auto;
  color: #102654;
  display: flex;
  flex-direction: column;
}
body .header,
body .footer {
  flex-shrink: 0;
}
body main {
  flex-grow: 1;
}

@media screen and (min-width: 1200px) {
  .header {
    background: conic-gradient(from 176deg at 20.71% 35.74%, #003d85 0deg, #b80027 262deg, #003d85 358deg);
  }
  .header__container {
    width: 1160px;
    margin: 0 auto;
    margin-top: 30px;
    position: relative;
  }
  .header__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .header__logo {
    font-size: 37px;
    line-height: 44px;
    font-weight: 600;
    font-family: "Alumni Sans", "Arial", sans-serif;
    font-style: normal;
    color: #ffffff;
  }
  .header__tel {
    font-size: 28px;
    line-height: 32px;
    font-weight: 600;
    font-family: "Alumni Sans", "Arial", sans-serif;
    font-style: normal;
    color: #ffffff;
  }
  .header__button-wrapper:hover {
    background: #ffffff;
  }
  .header__button-wrapper:hover .header__button {
    color: #103aae;
  }
  .header__button:focus {
    color: #103aae;
    background: #ffffff;
  }
  .header__button:disabled {
    opacity: 0.3;
  }
  .header__button {
    padding: 18px 57px;
    font-size: 12px;
    line-height: 14px;
    font-weight: 700;
    color: #ffffff;
    text-transform: uppercase;
  }
  .header__button-wrapper {
    width: 222px;
    height: 50px;
    left: 165px;
    bottom: 15px;
    position: absolute;
    border: 4px solid #ffffff;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 80px;
  }
  .header__button-wrapper:active {
    background: #ffffff;
  }
  .header__button-wrapper:active .header__button {
    color: #102654;
  }
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
  .header {
    background: conic-gradient(from 176deg at 20.71% 35.74%, #003d85 0deg, #b80027 262deg, #003d85 358deg);
  }
  .header__container {
    width: 678px;
    margin: 0 auto;
    margin-top: 34px;
  }
  .header__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .header__logo {
