@media screen and (min-width: $vp-1200) {
  .faq__container-title {
    margin: 0;
    font-size: 58px;
    margin-bottom: 59px;
    text-align: left;
    font-family: $alumni-sans-font;
    font-weight: 600;
  }

  .faq__container {
    width: 1160px;
    padding-bottom: 86px;
    margin: 0 auto;
    position: relative;
  }

  .faq {
    background-color: #f8f9fc;
    margin-bottom: 140px;
    padding-top: 141px;
  }

  .faq__tabs-item-title {
    padding-right: 20px;
    font-size: 24px;
    line-height: 29px;
    margin: 0;
    font-weight: 400;
    text-align: left;
  }

  .faq__tabs-item-toggle-minus {
    width: 18px;
    display: none;
  }

  .faq__tabs-item {
    margin-bottom: 62px;
    position: relative;

    &::after {
      width: 860px;
      bottom: -25px;
      content: "";
      height: 1px;
      background-color: rgb(28, 51, 116, 0.2);
      position: absolute;
    }

    &.is-active .faq__tabs-item-toggle {
      background-color: $base-color;
    }

    &.is-active .faq__tabs-item-toggle-plus {
      display: none;
    }

    &.is-active .faq__tabs-item-toggle-minus {
      display: block;
    }
  }

  .faq__tabs-item-toggle {
    width: 42px;
    height: 42px;
    top: -8px;
    right: 0;
    border-radius: 50%;
    border: 1px solid #1c3374;
    background-color: inherit;
    position: absolute;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
      background-color: $base-color;
    }

    &:hover .faq__tabs-item-toggle-plus {
      stroke: #ffffff;
    }

    &:focus-visible .faq__tabs-item-toggle-plus {
      stroke: #ffffff;
    }

    &:focus-visible {
      background-color: $base-color;
    }

    &:focus {
      outline: none;
    }
  }

  .faq__tabs-controls {
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    row-gap: 10px;
    column-gap: 0;
    position: relative;

    &::after {
      width: 860px;
      top: 0;
      right: -900px;
      bottom: -40px;
      content: "";
      height: 1px;
      background-color: rgb(28, 51, 116, 0.2);
      position: absolute;
    }
  }

  .faq__tabs-item-description {
    text-align: left;
    padding-right: 181px;
    font-size: 18px;
    line-height: 25px;
    margin-top: 16px;
  }

  .faq__tabs-control {
    width: 260px;
    height: 54px;
    font-weight: 400;
    font-size: 18px;
    font-family: $base-font;
    color: $base-color;
    background: inherit;
    border: 1px solid rgb(16, 38, 84, 0.2);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      border: 1px solid #102654;
    }

    &:focus {
      outline: none;
    }

    &:focus-visible {
      border: 1px solid #102654;
    }

    &.is-active {
      background: linear-gradient(140deg, #ed0233 19%, #003eb7 100%);
      border: none;
      color: #ffffff;
    }
  }

  .faq__tabs {
    display: flex;
    flex-direction: row;
  }

  .faq__tabs-content {
    width: 860px;
    margin-top: 32px;
    margin-left: 40px;
  }

  .faq__tabs-item-toggle-plus {
    width: 18px;
    height: 18px;
    stroke: $base-color;
  }
}


