  }
}

@media screen and (max-width: 767px) {
  .faq__container-title {
    font-family: $alumni-sans-font;
    font-size: 34px;
    font-weight: 600;
    text-align: center;
    margin: 0;
    margin-bottom: 36px;
  }

  .faq__tabs-item-title {
    margin: 0;
    font-size: 18px;
    line-height: 22px;
    font-weight: 400;
    text-align: left;
    padding-right: 73px;
  }

  .faq {
    background-color: #f8f9fc;
    margin-bottom: 74px;
    padding-top: 77px;
  }

  .faq__tabs-item-toggle-minus {
    display: none;
    width: 12px;
  }

  .faq__tabs-item-toggle-plus {
    stroke: $base-color;
  }

  .faq__tabs-item-toggle {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    border: 1px solid #1c3374;
    background-color: inherit;
    position: absolute;
    top: 11px;
    right: 0;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;


    &:active .faq__tabs-item-toggle-plus {
      stroke: #ffffff;
    }

    &:active {
      background-color: $base-color;
    }
  }

  .faq__tabs-item {
    position: relative;
    margin-bottom: 38px;

    &::after {
      content: "";
      width: 290px;
      height: 1px;
      background-color: rgb(28, 51, 116, 0.2);
      position: absolute;
      bottom: -18px;
    }

    &.is-active .faq__tabs-item-toggle {
      background-color: $base-color;
    }

    &.is-active .faq__tabs-item-toggle-plus {
      display: none;
    }

    &.is-active .faq__tabs-item-toggle-minus {
      display: block;
    }
  }

  .faq__container {
    width: 290px;
    margin: 0 auto;
    position: relative;
    padding-bottom: 50px;
  }

  .faq__tabs-controls {
    display: grid;
    grid-template-columns: 141px 141px;
    grid-template-rows: 44px 44px;
    column-gap: 8px;
    row-gap: 8px;
    margin-bottom: 50px;
    position: relative;

    &::after {
      content: "";
      width: 290px;
      height: 1px;
      background-color: rgb(28, 51, 116, 0.2);
      position: absolute;
      bottom: -30px;
    }
  }

  .faq__tabs-control {
    width: 141px;
    height: 44px;
    font-family: $base-font;
    font-weight: 400;
    font-size: 16px;
    color: $base-color;
    background: inherit;
    border: 1px solid rgb(16, 38, 84, 0.2);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      border: 1px solid #102654;
    }

    &:focus {
      outline: none;
    }

    &:focus-visible {
      border: 1px solid #102654;
    }

    &.is-active {
      background: linear-gradient(140deg, #ed0233 19%, #003eb7 100%);
      border: none;
      color: #ffffff;
    }
  }

  .faq__tabs-item-description {
    text-align: left;
    margin-top: 10px;
  }
}

@media screen and (min-width: 321px) and (max-width: 767px) {
  .faq__container {
    width: 90%;
  }
}

@media screen and (min-width: 400px) and (max-width: 767px) {
  .faq__tabs-item-description {
    padding-right: 10%;
  }
}
