@media screen and (min-width: $vp-1200) {
  .tabs {
    width: 1160px;
    margin: 0 auto;
  }

  .tabs__element-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .tabs__element-item {
    margin-top: 30px;
    width: 360px;
    background: #ffffff;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position: relative;

    &:hover::after {
      content: "";
      position: absolute;
      width: 347px;
      height: 434px;
      border: 7px solid;
      border-image: linear-gradient(120deg, #ed0233 0%, #003eb7 100%) 1;
      background: none;
    }

    &:hover .tabs__element-item-button-wrapper {
      background: linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
      border: none;
      width: 205px;
      height: 64px;
    }

    &:hover .tabs__element-item-button {
      color: #ffffff;
    }

    &:not(:last-child) {
      margin-right: 40px;
    }

    &:focus::after {
      content: "";
      position: absolute;
      width: 347px;
      height: 434px;
      border: 7px solid;
      border-image: linear-gradient(120deg, #ed0233 0%, #003eb7 100%) 1;
      background: none;
    }

    &:focus .tabs__element-item-button-wrapper {
      background: linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
      border: none;
      width: 205px;
      height: 64px;
    }

    &:focus .tabs__element-item-button {
      color: #ffffff;
    }
  }

  .tabs__element-item-button:active {
    opacity: 1;
  }

  .tabs__control {
    font-size: 20px;
    line-height: 30px;
    color: $base-color;
    background-color: inherit;
    font-family: $base-font;
    font-weight: 400;
    border: none;
    cursor: pointer;
    padding: 11px 2px 20px;
    opacity: 0.5;

    &.is-active {
      opacity: 1;
      pointer-events: none;
    }

    &:hover {
      opacity: 1;
    }

    &:focus {
      opacity: 1;
    }
  }

  .tabs__control:not(:last-child) {
    margin-right: 44px;
  }

  .tabs__controls {
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: relative;

    &::before {
      content: "";
      width: 341px;
      left: 410px;
      top: 51px;
      height: 2px;
      position: absolute;
      background: #787878;
      opacity: 0.5;
    }
  }

  .tabs__element-item-title {
    font-size: 26px;
    line-height: 40px;
    margin: 56px 0 0;
    font-weight: 700;
  }

  .tabs__element-item-info {
    margin-top: 6px;
    font-weight: 400;
  }

  .tabs__element-item-price-wrapper {
    margin: 0;
    margin-top: 64px;
    z-index: 1;
    position: relative;
  }

  .tabs__element-item-price {
    font-size: 60px;
    line-height: 90px;
    font-weight: 700;
    color: #103aae;
    margin-right: 5px;
  }

  .tabs__element-item-price-sign {
    width: 30px;
    height: 42px;
  }

  .tabs__element-item-button-wrapper {
    margin-top: 36px;
    margin-bottom: 68px;
    width: 198px;
    height: 56px;
    border: 4px solid #102654;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;

    &:focus {
      background: linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
      border: none;
      width: 205px;
      height: 64px;
    }

    &:focus .tabs__element-item-button {
      color: #ffffff;
    }
  }

  .tabs__element-item-button {
    font-size: 13px;
    line-height: 14px;
    font-weight: 700;
    color: $base-color;
    text-transform: uppercase;
    padding: 19px 41px;
  }

  .tabs__element-item-decoration-price {
    left: 90px;
    font-size: 100px;
    line-height: 150px;
    top: 135px;
    font-weight: 700;
    color: #e8e8e8;
    opacity: 0.5;
    position: absolute;
  }
}

