@media screen and (min-width: 769px) and (max-width: 1199px) {
  .tabs {
    width: 100%;
  }

  .tabs__controls {
    width: 76%;
    justify-content: space-between;
    margin: 0 auto;
  }

  .tabs__controls::before {
    width: 100%;
    left: 0;
    top: 46px;
  }

  .tabs__element-list {
    width: 100%;
  }

  .tabs__element-item {
    width: 100%;

    &:hover::after {
      width: 97%;
    }

    &:hover .tabs__element-item-button-wrapper {
      width: 70%;
    }
  }

  .tabs__element-item-button-wrapper {
    width: 70%;
  }
}

